import React from 'react';

const Language = ({language='Elvish', tooltipText='This is a tooltip.'}) => (
  <li>
    <a href="#" className="grey-text tooltipped" data-position="left" data-delay="50" data-tooltip={tooltipText}>
      {language}
    </a>
  </li>
)

export default class Languages extends React.Component {
  render() {
    const tooltipText = `This language isn't available yet.`;
    return (
      <div className="lang-links">
        <a
          className='dropdown-button btn-flat' 
          href='#' 
          data-activates='dropdown1'
          data-beloworigin="true"
          data-constrainwidth="false"
        >
          En
        </a>
        <ul id='dropdown1' className='dropdown-content'>
          <Language language='繁體中文' tooltipText={tooltipText} />
          <Language language='简体中文' tooltipText={tooltipText} />
          <Language language='한국어' tooltipText={tooltipText} />
          <Language language='日本語' tooltipText={tooltipText} />
        </ul>

      </div>
    );
  }
}
